on3.svg"),"alt":"AvatarTop"}}),_c('div',{staticClass:"tarjeta"},[_c('div',{staticClass:"p-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("Los objetivos deben ser flexibles. Es decir, se tienen que poder modificar o corregir en la medida de lo posible ante situaciones excepcionales o particulares que se le presenten a la organización, sean estas de carácter positivo o negativo. (Vilajoana, et al., 2014).")])])])])])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-12 align-self-center"},[_c('div',{staticClass:"tarjeta-avatar-b"},[_c('img',{staticStyle:{"align-self":"flex-start","margin-top":"25px"},attrs:{"src":require("@/assets/curso/tema1/tarjetas/num4.svg"),"alt":"AvatarNum"}}),_c('div',{staticClass:"tarjeta transparent1"},[_c('div',{staticClass:"p-4"},[_c('div',{staticClass:"row mb-1"},[_c('div',{staticClass:"col-lg-6"},[_c('div',{staticClass:"h4 mb-4"},[_vm._v("Objetivos de medios (cuantitativos)")]),_c('p',{staticClass:"mb-2"},[_vm._v("Por su parte, los objetivos de medios tienen un enfoque cuantitativo, puesto que su direccionamiento es la consecución de impactos del mensaje sobre el mercado objetivo; es decir, a cuántas personas va a llegar el mensaje, durante cuánto tiempo y la cantidad de veces en determinado tiempo: días, semanas, meses. Esto permite generar indicadores para realizar, posteriormente, una medición adecuada de la campaña y contrastar los resultados con otras variables como el aumento en las ventas y el retorno de la inversión. Los objetivos de medios pueden tener enfoque en los siguientes aspectos:")])]),_c('div',{staticClass:"col-lg-6"},[_c('figure',[_c('img',{staticClass:"mx-auto",staticStyle:{"width":"380px"},attrs:{"src":require("@/assets/curso/tema1/tarjetas/img4.svg"),"alt":"Objetivos de medios (cuantitativos):"}})])])])])])])])]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-10",staticStyle:{"border-bottom":"2px solid #c5c5c5","padding":"0"}},[_c('div',{staticClass:"ta